ol li {
  margin: 0px 5px;
  counter-increment: item;
}
ol li a {
  padding: 10px;
  color: var(--lightest-slate);
}
ol li a::before {
  content: "0" counter(item) ".";
  margin-right: 5px;
  color: var(--green);
  font-size: var(--fz-xxs);
}
a {
  color: inherit;
  text-decoration: none;
}
.navbar__header {
  display: flex;
  justify-content: space-between;
  position: relative;
  align-items: center;
  width: 100%;
  height: 100px;
}
